body {
  font-family: arial, helvetica;
  color: #f2f2f2;
}
h1 {
  font-size: 6em;
}
p {
  font-size: 2em;
}
.content {
  position: relative;
  /* top: 50%;
  transform: translateY(-50%); */
  text-align: center;
}
/* Section 1
 * --------------------------------------- */
#section0 {
  background-color: #bfda00;
}
#section0 h1 {
  color: #444;
}
#section0 p {
  color: #333;
  opacity: 0.4;
}
/* Section 2
 * --------------------------------------- */
#section1 {
  display: flex;
  flex-direction: row;
}
#section1 h1 {
  color: #fff;
}
#section1 p {
  opacity: 0.8;
}
/* Section 3
 * --------------------------------------- */
#section2 {
  background-color: #2c3e50;
}
#section2 h1 {
  color: #f2f2f2;
}
#section2 p {
  opacity: 0.6;
}

